<template>
  <tas-base-crud :config="config" hide-delete hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="$children[0].getEvent('detail', data.rowData)" class="btn btn-info btn-sm"> Lihat Detail Pengguna </a>
      <a v-if="data.rowData.status_code === 'user_active'" @click="changeUserStatus(data.rowData, 'user_nonactive')" class="btn btn-icon btn-danger btn-sm" v-b-tooltip.viewport="'Klik untuk menonaktifkan pengguna'"> <i class="ri-shut-down-line"></i> </a>
      <a v-if="data.rowData.status_code === 'user_nonactive'" @click="changeUserStatus(data.rowData, 'user_active')" class="btn btn-icon btn-success btn-sm" v-b-tooltip.viewport="'Klik untuk mengaktifkan pengguna'"> <i class="ri-shut-down-line"></i> </a>
    </template>
    <template v-slot:detail-footer="data">
      <div v-if="data.rowData" class="row">
        <div class="col-lg-12" v-if="$_sys.isAllowed('update-mapping-users-divisions')">
          <mapping-input :alert="['Non-Aktifkan Divisi','Proyek dibawah Divisi juga akan di non-aktifkan']" @change="requestReload(data.rowData.id)" :title="mappingDivision.title" :getter="mappingDivision.getter" :setter="mappingDivision.setter" :params="{ 'user_id': data.rowData.id }" :readonly="mappingDivision.readonly" :display="mappingDivision.display" :setter_id="mappingDivision.setter_id" :key_value="mappingDivision.key_value" />
        </div>
        <!-- <div class="col-lg-12 mt-5" v-if="$_sys.isAllowed('create-mapping_users_projects')">
          <mapping-input :refresh="refreshToken" :title="mappingProjects.title" :getter="mappingProjects.getter" :setter="mappingProjects.setter" :params="{ 'user_id': data.rowData.id }" :readonly="mappingProjects.readonly" :display="mappingProjects.display" :setter_id="mappingProjects.setter_id" :key_value="mappingProjects.key_value" />
        </div> -->
      </div>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-users',
  data () {
    return {
      config: {
        title: 'Daftar Pengguna SSO',
        model_api: 'users',
        getter: 'users',
        setter: 'users',
        pk_field: 'fullname',
        export: true,
        permission: {
          create: 'create-users',
          read: 'view-users',
          update: 'update-users',
          delete: 'delete-users'
        },
        fields: [
          {
            id: 'fullname',
            label: 'Nama Lengkap',
            methods: {
              list: {
                type: ['file', 'text'],
                order: [false, true],
                view_data: ['img_photo_user.url', 'fullname'],
                class: ['mr-2', 'col p-0'],
                transform: []
              }
            }
          },
          { id: 'telephone', label: 'No. Telp', methods: { list: false } },
          { id: 'job_position', label: 'Jabatan', methods: { list: false } },
          { id: 'email_verified_at', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'sso_user_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'approval_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'approval_at', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'api_token', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'password', methods: { list: false, detail: false, create: true, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_at', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'last_login_at', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'approval_description', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'register_division_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'register_project_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'nip', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          {
            id: 'img_photo_user',
            label: 'Foto Pengguna',
            methods: {
              list: false,
              detail: false,
              // detail: {
              //   type: 'file',
              //   view_data: 'img_photo_user.url'
              // },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*' }, { limit: 5 }],
                view_data: 'img_photo_user'
              }
            }
          },
          {
            id: 'role_id',
            label: 'Role',
            methods: {
              create: {
                setter: 'roles',
                getter: 'roles',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'role_name', code: 'id', display: ['role_name']
                  }
                }
              },
              update: {
                setter: 'roles',
                getter: 'roles',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'role_name', code: 'id', display: ['role_name']
                  }
                }
              },
              filter: {
                setter: 'roles',
                getter: 'roles',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'role_name', code: 'id', display: ['role_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_code',
            data: 'status_code',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { user_active: 'badge badge-primary', user_nonactive: 'badge badge-warning' },
                transform: 'user_status_code'
              },
              detail: {
                order: true,
                class: { user_active: 'badge badge-primary', user_nonactive: 'badge badge-warning' },
                transform: 'user_status_code'
              },
              create: false,
              update: false,
              filter: false
            }
          }
        ]
      },
      refreshToken: 0,
      projectFilter: {
        division_id: null
      },
      mappingProjects: {
        title: 'Proyek Pengguna',
        getter: 'custom/mapping-users-projects',
        setter: 'custom/mapping-users-projects',
        display: ['project_number', 'project_name'],
        key_value: 'active',
        setter_id: 'project_id',
        readonly: false
      },
      mappingDivision: {
        title: 'Divisi Pengguna',
        getter: 'custom/mapping-users-divisions',
        setter: 'custom/mapping-users-divisions',
        display: ['division_name'],
        key_value: 'active',
        setter_id: 'division_id',
        readonly: false
      }
    }
  },
  methods: {
    requestReload (data) {
      this.refreshToken++
    },
    changeUserStatus (data, status) {
      data.status_code = status
      this.$_api.post('users/active-deactive', data).then(res => {
        this.$_alert.success(null, res.message)
        this.$children[0].$children[1].getData(null)
      }, err => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>

<style>
</style>



